import React from 'react'
import { Typography,List,ListItem,ListItemText } from '@material-ui/core'

const Review = ({checkoutToken}) => {
    return (
        <>
            <Typography variant="h4" gutterBottom>Đơn hàng của bạn</Typography>
            <List disablePadding>
{checkoutToken.live.line_items.map((product)=>(
<ListItem style={{padding:'10px 0'}} key={product.name}>

<ListItemText primary={product.name} secondary={`SL:${product.quantity}`} />
<Typography variant="body2">{product.line_total.formatted_with_code} </Typography>

</ListItem>


))}
<ListItem style={{padding:'10px 0'}}>
    <ListItemText primary="Tổng"/>
    <Typography variant="h5" style={{fontWeight:550}}>
{checkoutToken.live.subtotal.formatted_with_symbol}
    </Typography>

</ListItem>



            </List>
        </>
    )
}

export default Review
