import React from 'react'
import {Container,Typography,Button,Grid} from '@material-ui/core'
import  useStyles  from './styles'
import CartItem from './CartItem/CartItem'
import {Link} from 'react-router-dom'
const Cart =({cart,handleUpdateCartQty,handleRemoveFromCart,handleEmptyCart})=>{
const classes=useStyles()
const EmptyCart =()=>(

<Typography variant="h6">Giỏ hàng của bạn đang trống. Hãy bắt đầu
<Link to="/" className={classes.link}> mua sắm ngay</Link>
</Typography>

)
const FilledCart =()=>(
<>
<Grid container spacing={3}>
{cart.line_items.map((item)=>(


<Grid item xs={12} sm={4} key={item.id}>
<CartItem item={item} onUpdateCartQty={handleUpdateCartQty} onRemoveFromCart={handleRemoveFromCart}/>


</Grid>
))}



</Grid>
<div className={classes.cardDetails}>
<Typography variant="h3">
Tổng :{cart.subtotal.formatted_with_code}
</Typography>
<div>
    <Button className={classes.emptyButton} size="large" type="button" variant="contained" color="secondary" onClick={handleEmptyCart}>Xóa giỏ hàng</Button>
    <Button className={classes.checkoutButton} component={Link} to="/checkout" size="large" type="button" variant="contained" color="primary">Thanh Toán</Button>
</div>


</div>


</>


)

if(!cart.line_items) return 'Loading'
    return(
<Container>
<div className={classes.toolbar} />
<Typography className={classes.title} variant="h4" gutterBottom>Giỏ Hàng</Typography>
{!cart.line_items.length ? <EmptyCart /> : <FilledCart />}
</Container>


    )
}
export default Cart